html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
:root {
  font-size: 62.5%;
}
body {
  background-color: #555555;
  color: antiquewhite;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
#appDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.wrapper { 
    background:#111;
    margin: auto; 
    text-align: center; 
    position: relative;
    border-radius: 2rem;
    margin-bottom: 20px !important;
    width: 70vw;
    padding: 1rem;
}
.scrolls { 
    overflow-x: scroll;
    overflow-y: hidden;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    white-space: nowrap;
    padding: 1.5rem;
} 
img { 
    border: 0.03rem solid #444;
    border-radius: 0.6rem;
    margin-left: 0.7rem;
    margin-right: 0.7rem; 
    height: 70vh;
    cursor: pointer;
    display:inline-block;
    *display:inline;
    *zoom:1;
    vertical-align:top;
}
h3#socialsTag {
    color: brown;
    border: black solid 0.2rem;
    width: 50vw;
    padding: 2rem;
    border-radius: 1.5rem;
    background-color: #888;
}
h3#introTag {
    color: teal;
    border: black solid 0.2rem;
    width: 50vw;
    padding: 2rem;
    border-radius: 1.5rem;
    background-color: #888;
}
h3#aboutTag {
    color: tomato;
    border: black solid 0.2rem;
    width: 50vw;
    padding: 2rem;
    border-radius: 1.5rem;
    background-color: #888;
}
